.header-details-greet{justify-content:flex-start;align-items:center}
#details{background:linear-gradient(140deg,navy, red);height:100vh}
.holder-name h5{color:white}
.holder-name{color:gold}
.balanceAcc h5 {color:white}
.balanceAcc{color:gold}
.inp-credit{background:transparent;border:none;border-bottom:1px solid white;outline:none;color:white;width:80%}
.inp-credit::placeholder{color:white}
.inp-debit{background:transparent;border:none;border-bottom:1px solid white;outline:none;color:white;width:80%}
/* .inp-debit::placeholder{color:white} */
.credit-span,.debit-span{color:white;}
/* .inp-credit:focus .credit-span{diplay:flex}
.inp-debit:focus .debit-span{diplay:flex} */
.credit-btn{height:25px;width:50px;background:green;color:white;border-radius:20px;border:1px solid greenyellow;margin-top:10px}
.credit-btn:hover{background:greenyellow;color:green}
.debit-btn{height:25px;width:50px;background:red;color:white;border-radius:20px;border:1px solid darkred;margin-top:10px}
.debit-btn:hover{background:darkred;color:red ;border: 1px solid red}
.transaction-div{display:flex;justify-content:flex-end;align-items:center;}
.transaction-btn{height:35px;width:90px;border-radius:20px; background:linear-gradient(45deg,red,navy);color:white;border:1px solid white}
.transaction-btn:hover{background:linear-gradient(195deg,red,navy);}
.ToAccountNumber,.IFSCNumber,.AccountHolderName{background:transparent;border:none;border-bottom:1px solid white;outline:none;color:white;width:80%}