.header-logo{justify-Content:space-between}
#login-bank{background:linear-gradient(40deg,navy, red);height:100vh}
.form-content{}
.login-body{height:80vh;display:flex;justify-content:center;align-items:center}
form{background:rgba(255,255,255,.5);max-width:300px;padding:25px;border-radius:10px;}
.login-header{color:maroon}
.button-div-log{display:flex;justify-content:flex-end;margin-top:15px;}
.button-div-log button{width:75px;height:35px;background:blue;color:white;border-radius:50px;border:maroon}
.button-div-log button:hover{background:maroon;color:white}
.button-div-log button:active{background:yellowgreen;color:black}